import React, {FC, useEffect, useRef} from 'react';
import {motion, useAnimation, useInView} from "framer-motion";
import './Reveal.scss'

interface RevealProps {
    children: React.ReactNode,
    type?: "default" | "slideFromLeft" | "slideFromRight" |
        "slideFromTop" | "slideFromBottom",
    background: boolean,
    width?: "fit-content" | "100%"
}

const Reveal: FC<RevealProps> = ({children, type = "default", background = false, width = "fit-content"}) => {

    const ref = useRef(null)
    const isInView = useInView(ref, {once: true})
    const typeControls = useAnimation()
    const backgroundControls = useAnimation()

    useEffect(() => {
        if (isInView) {
            typeControls.start({opacity: 1, x: 0, y: 0})
            backgroundControls.start("visible")
        }
    }, [isInView])


    return (
        <div ref={ref} style={{width, background: "inherit", display: "inline-block",  position: "relative", overflow: "hidden"}}>
            <motion.div
                style={{textAlign: "end",}}
                variants={{
                    default: {
                        opacity: 0
                    },
                    slideFromLeft: {
                        x: "-100",
                    },
                    slideFromRight: {
                        x: "100"
                    },
                    slideFromTop: {
                        y: "-30",
                        opacity: 0
                    },
                    slideFromBottom: {
                        y: "30",
                        opacity: 0
                    }
                }}
                transition={{duration: 0.5, delay: 0.25}}
                initial={type === "slideFromLeft" ? "slideFromLeft" :
                    type === "slideFromRight" ? "slideFromRight" :
                        type === "slideFromTop" ? "slideFromTop" :
                            type === "slideFromBottom" ? "slideFromBottom" :
                                "default"
                }
                animate={typeControls}
            >
                {children}
            </motion.div>
            <motion.div
                variants={{
                    hidden: {left: 0},
                    visible: {left: "100%"}
                }}
                initial="hidden"
                animate={backgroundControls}
                transition={{duration: 0.5, ease: "easeIn"}}
                className="reveal"
                style={{
                    position: "absolute",
                    height: "auto",
                    top: 4,
                    bottom: 4,
                    right: 0,
                    left: 0,
                    zIndex: 20
                }}

            >
            </motion.div>
        </div>
    );
};

export default Reveal;